<template>
  <div class="game d-flex align-items-center">
    <div>
      <div class="text-white h8 text-center mb-2">Time: {{time}} Score: {{score}}</div>
      <div class="game-content d-flex flex-wrap w-75 mx-auto mb-5">
        <div v-for="(items,index) in 9" :key="index" class="game-items m-2 bg-white position-relative">
          <transition name="fade">
            <div :id="`game_item_${index}`" class="d-none p-2 position-absolute game-click-item" @click="getScore(index)">
              <img src="../assets/img/u-money.png" width="100%" alt="">
            </div>
          </transition>
          <div class="p-2">
            <img src="../assets/img/u-money2.png" width="100%" alt="">
          </div>
          <!-- <div>o</div> -->
        </div>
      </div>
      <div class="start-btn p-3 text-white mx-auto" @click="start">start</div>
    </div>
    <div class="model d-flex align-items-center justify-content-center" v-if="finished">
      <div>
        <div class="text-white h8 text-center mb-5">Your Score : {{score}}</div>
        <div class="start-btn p-3 text-white mx-auto" @click="gamefinish">close</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'game',
  data () {
    return {
      score: 0,
      timer: null,
      timer2: null,
      alltimer: null,
      countdown: null,
      numarr: [],
      time: 30,
      playing: false,
      finished: false
    }
  },
  created () {
    this.newarray()
  },
  mounted () {
  },
  watch: {
    time () {
      if (this.time === 0) {
        clearInterval(this.countdown)
        clearInterval(this.timer)
        clearTimeout(this.timer2)
        console.log(this.time)
        this.alltimer = setTimeout(() => {
          this.finished = true
        }, 1000)
      }
    }
  },
  methods: {
    getScore (index) {
      // console.log(this, document.)
      this.score += 1
      document.querySelector(`#game_item_${index}`).classList.add('d-none')
      console.log(this.score)
    },
    gamefinish () {
      this.playing = false
      this.finished = false
      this.score = 0
    },
    newarray () {
      for (let i = 0; i < 100; i++) {
        this.numarr.push(Math.floor(Math.random() * 9))
        if (this.numarr[i] === this.numarr[i - 1]) {
          this.numarr[i] = Math.floor(Math.random() * 9)
        }
      }
    },
    start () {
      if (this.playing) return
      this.playing = true
      this.time = 30
      this.countdown = setInterval(() => {
        this.time -= 1
      }, 1000)
      let i = 0
      this.timer = setInterval(() => {
        let j = i
        document.querySelector(`#game_item_${this.numarr[i]}`).classList.remove('d-none')
        this.timer2 = setTimeout(() => {
          document.querySelector(`#game_item_${this.numarr[j]}`).classList.add('d-none')
        }, 1000)
        i++
      }, 500)
    }
  }
}
</script>

<style>
.game{
  height: 100vh;
  min-height: 100%;
  background-color: rgb(169, 216, 243);
}
.game-items{
  width: calc((100% / 3) - 1rem) ;
}
@keyframes move {
  0% {
    opacity: 0.5;
    transform: translateY(5px);
  }
  50% {
    opacity: 0.8;
    transform: translateY(2px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
.game-click-item{
  animation: move 1s ease-in;
  /* z-index: 999; */
}
.start-btn{
  border: 1px solid #ffffff;
  width: fit-content;
}
.model{
  height: 100vh;
  width: 100%;
  min-height: 100%;
  background-color: rgba(66, 66, 66, 0.575);
  position: absolute;
  top: 0;
  left: 0;
}
</style>



